import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  import About from './components/About'
  import Contact from './components/Contact'
  import Gallery from './components/Gallery'
  import Home from './components/Home'

ReactDOM.render(
    <div className="index">
<Router>
{/* style={{backgroundColor: "darkslategrey",color: "white"}}  */}
<header class="text-gray-600 body-font">
  <div class="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center ">
    <p class="flex title-font font-medium items-center text-gray-900 mb-4 md:mb-0" >
      <svg xmlns="http://www.w3.org/2000/svg" fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" class="w-10 h-10 text-white p-2 bg-indigo-500 rounded-full" viewBox="0 0 24 24">
        <path d="M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5"></path>
      </svg>
      <span class="ml-3 text-xl">GearOn</span>
    </p>
    <nav class="md:ml-auto md:mr-auto flex flex-wrap items-center text-base justify-center">    
      <p class="mr-5 hover:text-gray-900"><Link to="/">Home</Link></p>
      <p class="mr-5 hover:text-gray-900"><Link to="/About">About</Link></p>
      <p class="mr-5 hover:text-gray-900"><Link to="/Contact">Contact</Link></p>
      <p class="mr-5 hover:text-gray-900"><Link to="/Gallery">Notification</Link></p>
    </nav>
   
  </div>
</header>
<Switch>
          <Route path="/about">
            <About name="Aman Singh"/>
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
</Router>

    </div> ,document.getElementById("root"));